export default {
  data: () => ({
    valid: true,
    name: "",
    nameRules: [v => !!v || "Name is required"],
    totalPlayers: 2,
    totalPlayerRules: [
      v => !!v || "Number of players is required",
      v => v >= 2 || "There should be at least 2 players"
    ],
    type: "knockout"
  }),
  methods: {
    submit() {
      if (!this.$refs.form.validate()) {
        return;
      }

      this.$store.commit("addTournament", {
        name: this.name,
        type: this.type,
        numberOfPlayers: this.totalPlayers
      });

      this.$router.push('/');
    }
  }
};